.App {
  text-align: center;
}
.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}
.App-link {
  color: #61dafb;
}
#root {
  margin:auto;
  width: 50%;
  text-align: center;  
}
.table {
  width: 50%; 
  margin:auto;  
}
.product {
  width: 75%;
}
/* Style the form - display items horizontally */
.form-inline {
  display: flex;
  flex-flow: nowrap row;
  align-items: center; 
  justify-content: center;  
  margin:10px auto;  
}
/* Add responsiveness - display the form controls vertically instead of horizontally on screens that are less than 800px wide */
@media (max-width: 800px) {
  .form-inline input {
    margin: 10px 0;
  }

  .form-inline {
    flex-direction: column;
    align-items: stretch;
  }
}
.sr-only {
  display: none;
}



